<template>
  <div style="padding : 10px">
    <h4>ÌåîÎ°úÏõå</h4>
    <input type="text" placeholder="üîç" @input="searchUser" />
    <div class="post-header" v-for="(followerData, i) in sFollower" :key="i">
      <div class="profile" :style="{ backgroundImage: `url(${followerData.image})` }"></div>
      <span class="profile-name">{{ followerData.name }}</span>
    </div>
  </div>
</template>

<script>
import { computed, onMounted, ref, toRefs, watch } from 'vue'
import axios from 'axios'
import { useStore } from 'vuex'

export default {
  name: "MyPage",
  props: {
    one: Number
  },
  setup(props) {
    // ÏùºÎ∞òÏ†ÅÏù∏ Î≥ÄÏàò ÏÑ†Ïñ∏Î≤ï
    let follower = ref([]);
    let sFollower = ref([]);
    // reactive Ïù¥Îü∞ Ìï®ÏàòÎèÑ ÏûàÏùå => refÎûë Í±∞Ïùò ÎòëÍ∞ôÏùÄÎç∞ Í¥ÄÏäµÏ†ÅÏúºÎ°ú refÎäî primitive dataÎ•º Ïì∞Í≥† reactiveÎäî reference dataÎ•º ÏîÄ
    // Í∑ºÎç∞ ÏïàÏóê ÏΩîÎìúÎ•º ÍπåÎ≥¥Î©¥ Ïñ¥Ï∞®Ìîº refÎûë reactiveÎûë Ï∞®Ïù¥Í∞Ä ÏóÜÍ∏∞ ÎïåÎ¨∏Ïóê ref ÌïòÎÇòÎßå Ïç®ÎèÑ ÏÉÅÍ¥ÄÏóÜÏùå
    // let test = reactive({
    //   name: 'kim'
    // });

    // props Î≥ÄÏàò ÏÇ¨Ïö©Î≤ï
    // Î≥ÄÏàòÏóê Ï§ëÍ¥ÑÌò∏ ÏπòÎäîÍ±∞ => ES6Ïùò Destructuring Î¨∏Î≤ïÏûÑ
    let { one } = toRefs(props); // props ÏÇ¨Ïö©Ìï† Îïê toRefs ÏÇ¨Ïö©ÌïòÎäîÍ≤å Í¥ÄÏäµÏ†ÅÏûÑ
    console.log(`propsÎ°ú Î∞õÏùÄ Îç∞Ïù¥ÌÑ∞Îäî ${one.value}ÏûÖÎãàÎã§`);

    // watch Ìï®Ïàò ÏÇ¨Ïö©Î≤ï
    // Ï≤´Î≤àÏß∏ Ïù∏ÏûêÎäî watchÎ•º Ìï† Î≥ÄÏàò, ÎëêÎ≤àÏß∏ Ïù∏ÏûêÎäî Í∑∏ Î≥ÄÏàòÍ∞Ä Î≥ÄÌïòÎ©¥ Ïã§Ìñâ Ìï† Ìï®Ïàò
    watch(one, () => {

    });

    // computed ÏÇ¨Ïö©Î≤ï
    let re = computed(() => {
      return follower.value.length;
    });
    console.log(re.value);
    
    // store Î≥ÄÏàò ÏÇ¨Ïö©Î≤ï
    // Composition APIÏóêÏÑúÎäî mapState, mapMutations, mapActions Î™ªÏîÄ..
    let store = useStore();
    console.log(store.state.age);
    // console.log(store.commit());

    // methods ÏÇ¨Ïö©Î≤ï
    function searchUser(e) {
      if (e.target.value == '') {
        sFollower.value = [...follower.value];
      } else {
        sFollower.value = follower.value.filter(data => {
          return data.name.includes(e.target.value);
        });
      }
    }

    onMounted(() => {
      axios.get('/follower.json').then((result) => {
        follower.value = result.data;
        sFollower.value = [...follower.value];
      }).catch((error) => {
        console.log(error.message);
      });
    });

    return { follower, sFollower, searchUser };
  }
