<template>
  <div class="header">
    <ul class="header-button-left">
      <li>Cancel</li>
    </ul>

    <ul class="header-button-right">
      <li v-if="step == 3" @click="writePost">Í≤åÏãú</li>
      <li v-else @click="writePost">Next</li>
    </ul>

    <img src="./assets/logo.png" class="logo" />
  </div>

  <!-- <p>{{ ÎçîÎ≥¥Í∏∞Îç∞Ïù¥ÌÑ∞ }}</p> -->

  <!-- <h4>ÏïàÎÖï {{ $store.state.age }}</h4> -->
  <!-- <button @click="Îç∞Ïù¥ÌÑ∞Í∞ÄÏ†∏Ïò§Í∏∞()">Î≤ÑÌäº</button> -->

  <!-- <p>{{ $store.state.more }}</p>
  <button @click="$store.dispatch('getData')">ÎçîÎ≥¥Í∏∞Î≤ÑÌäº</button> -->

  <Container @writeContent="tempData.content = $event" @writeName="tempData.name = $event"
    :step="step" :uploadImageURL="tempData.postImage" :filter="tempData.filter" />

  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>
</template>

<script>
import Container from './components/Container.vue'
import { mapActions,  mapMutations,  mapState } from 'vuex'

export default {
  name: "App",
  data() {
    return {
      step: 0,
      tempData: {
        name: "",
        userImage: "https://placeimg.com/100/100/arch",
        postImage: "",
        likes: 0,
        date: "May 15",
        content: "",
        filter: ""
      }
    };
  },
  mounted() {
    // mitt ÎùºÏù¥Î∏åÎü¨Î¶¨ ÏÇ¨Ïö© => ÎåÄÎ∂ÄÎ∂ÑÏùÄ mounted ÏïàÏóê Ï†ÅÏùå
    this.emitter.on('changeFilter', (filter) => {
      this.tempData.filter = filter;
    });
  },
  // Ï¢ÖÏÜçÎêú Í∞íÏóê ÏùòÏ°¥Ìï® => data(state) ÏÜçÏÑ±Ïóê Ï†ïÏùòÌïú Î∞òÏùëÌòï Îç∞Ïù¥ÌÑ∞Î•º returnÌïòÎ©¥ Í∑∏ Í∞íÏóê Ï¢ÖÏÜçÎêòÏñ¥ ÏûàÎäîÍ±∞ÏûÑ => Í∑∏ Í∞íÏù¥ Î≥ÄÌïòÎ©¥ ÏóÖÎç∞Ïù¥Ìä∏ Îê®
  // Ï¢ÖÏÜçÎêòÏñ¥ ÏûàÏßÄ ÏïäÏúºÎ©¥ Ïû¨ÎûúÎçîÎßÅÏãúÏóêÎèÑ Îã§Ïãú Ïã§ÌñâÎêòÏßÄ ÏïäÏùå => ÏóÖÎç∞Ïù¥Ìä∏ ÎêòÏßÄ ÏïäÏùå
  computed: {
    name() { // computedÏóêÏÑú state Î≥ÄÏàòÎ•º Í∞ñÎã§ Ïì∞Îäî Í∞ÄÏû• Í∏∞Ï¥àÏ†ÅÏù∏ Î∞©Ïãù
      return this.$store.state.name;
    },
    ...mapState([ // state Î≥ÄÏàòÎ•º Í∞ñÎã§ Ïì∞Í≥† Ïã∂ÏùÑ Îïå
      "name", "age", "more"
    ]),
    ...mapState({ // state Î≥ÄÏàòÎ•º Îã§Î•∏ Ïù¥Î¶ÑÏúºÎ°ú Í∞ñÎã§ Ïì∞Í≥† Ïã∂ÏùÑ Îïå
      ÎÇ¥Ïù¥Î¶Ñ: "name",
      ÎçîÎ≥¥Í∏∞Îç∞Ïù¥ÌÑ∞: "more"
    })
  },
  // Ï¢ÖÏÜçÎêòÏñ¥ ÏûàÎÉêÎ•º ÏÉÅÍ¥ÄÌïòÏßÄ ÏïäÍ≥† Ïû¨ÎûúÎçîÎßÅÏãúÏóê Í≥ÑÏÜç ÏóÖÎç∞Ïù¥Ìä∏ Îê® => Ïû¨Ìò∏Ï∂úÎê®
  methods: {
    ...mapMutations([ // mutations Ìï®ÏàòÎ•º Í∞ñÎã§ Ïì∞Í≥† Ïã∂ÏùÑ Îïå
      "changeName"
    ]),
    ...mapMutations({ // mutations Ìï®ÏàòÎ•º Îã§Î•∏ Ïù¥Î¶ÑÏúºÎ°ú Í∞ñÎã§ Ïì∞Í≥† Ïã∂ÏùÑ Îïå
      ÎÇòÏù¥ÏóÖ: "plusAge"
    }),
    ...mapActions([ // actions Ìï®ÏàòÎ•º Í∞ñÎã§ Ïì∞Í≥† Ïã∂ÏùÑ Îïå
      "getData"
    ]),
    ...mapActions({ // actions Ìï®ÏàòÎ•º Îã§Î•∏ Ïù¥Î¶ÑÏúºÎ°ú Í∞ñÎã§ Ïì∞Í≥† Ïã∂ÏùÑ Îïå
      Îç∞Ïù¥ÌÑ∞Í∞ÄÏ†∏Ïò§Í∏∞: "getData"
    }),
    upload(e) { // Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ïãú Ïã§Ìñâ
      let inputFile = e.target.files;
      this.tempData.postImage = URL.createObjectURL(inputFile[0]);
      this.tempData.filter = "";
      this.step++;
    },
    writePost() { // Í∏Ä ÏûëÏÑ± Î≤ÑÌäº ÌÅ¥Î¶≠ Ïãú Ïã§Ìñâ
      if (this.step == 0) {
        return;
      }
      if (++this.step == 4) {
        this.$store.commit('writePost', {...this.tempData});
        this.step = 0;
      }
    }
  },
  components: {
    Container: Container
  }
};
</script>

