import { createStore } from 'vuex'
import instaData from './assets/instaData.js'
import axios from 'axios'

const store = createStore({
  state(){
    return {
      name: "kim",
      age: 20,
      data: [...instaData],
      more: {}
    }
  },
  mutations: {
    changeName(state) {
      state.name = "Î∞ï";
    },
    plusAge(state, num) {
      state.age += num;
    },
    clickLikes(state, i) {
      if (!state.data[i].liked) { // Ï¢ãÏïÑÏöî ÎàÑÎ•¥ÏßÄ ÏïäÏùÄ ÏÉÅÌÉú
        state.data[i].likes++;
        state.data[i].liked = true;
      } else { // Ï¢ãÏïÑÏöî ÎàÑÎ•∏ ÏÉÅÌÉú
        state.data[i].likes--;
        state.data[i].liked = false;
      }
    },
    writePost(state, postData) {
      state.data.unshift(postData);
    },
    setMore(state, data) {
      state.more = data;
    }
  },
  actions: { // { commit } Ïù¥Îü∞ÏãùÏúºÎ°ú Ïù∏ÏûêÎ•º Î∞õÏúºÎ©¥ commit Ìï®ÏàòÎ•º Î∞îÎ°ú Ïì∏ Ïàò ÏûàÏùå
    getData(context) { // context == $store
      axios.get("https://codingapple1.github.io/vue/more0.json").then((result) => {
        context.commit('setMore', result.data);
      });
    }
  }
});

export default store;